import isEmpty from "./isEmpty"


const validateItemCreation = (itemData) => {
    const errors = {}


    if (isEmpty(itemData.name)) {
        errors.name = 'Item Name cannot be empty.'
    }
    if (isEmpty(itemData.cp)) {
        errors.cp = 'Cost Price can\'t be empty.'
    }
    if (isEmpty(itemData.sp)) {
        errors.sp = 'Sales Price can\'t be empty.'
    }
    if (isEmpty(itemData.category)) {
        errors.category = 'Item must have a category.'
    }


    if (isEmpty(itemData.barcode)) {
        errors.barcode = 'Barcode field can\'t be empty.'
    }

    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}

const validateItemCategoryCreation = (itemCategory) => {
    const errors = {}

    if (isEmpty(itemCategory.catName)) {
        errors.catName = 'Category Name cannot be empty.'
    }

    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}
export { validateItemCreation, validateItemCategoryCreation }
