import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown, Badge } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import { validateItemCategoryCreation, validateItemCreation } from '../../validation/items';
import ErrorBody from '../../App/components/Reusables/ErrorBody';
import { getItemCatagories, getItemCategory, addItemCategory } from '../../api/inventory/itemCategory';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import Loader from '../../App/components/Reusables/Loader';


const Create = (props) => {


    const [loading, setLoading] = React.useState(true)
    const [submitting, setSubmitting] = React.useState(false);

    const [parentLoading, setParentLoading] = React.useState(false);

    const [itemCategory, setItemCategory] = React.useState({ catName: '', parentId: '' });
    //root catagoriesf
    const [currentCats, setCurrentCats] = React.useState([])

    const [selectedRoute, setSelectedRoute] = React.useState([])

    const [errors, setErrors] = React.useState({});



    //Did mount
    React.useEffect(() => {

        const controller = new AbortController();

        getItemCatagories({ 'action': 'get', 'start': 0, 'end': 999999, filter: 'parent' }, controller.signal)
            .then(async res => {
                console.log(res);
                if (res.json.status) {
                    await setCurrentCats(res.json.item_catagories)
                    setLoading(false)
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch(err => {
                console.log(err)
            })
        return () => controller.abort();
    }, []);


    //Get child categories
    React.useEffect(() => {
        //Get the child categories of selected category;
        if (selectedRoute.length > 0) {
            setParentLoading(true);
            const requestJson = {
                action: 'get',
                item_category_id: selectedRoute.length > 0 ? selectedRoute[selectedRoute.length - 1].id : null
            }

            getItemCategory(requestJson)
                .then(async (res) => {
                    console.log(res)
                    if (res.json.status) {
                        await setCurrentCats([...res.json.sub_categories]);
                        await setItemCategory({ ...itemCategory, parentId: '' })
                        await setParentLoading(false)
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });
        }
    }, [selectedRoute])

    const amendErrorOf = (field) => {
        setErrors({ ...errors, [field]: '' })
    }

    const _change = (e) => {
        e.preventDefault();
        amendErrorOf(e.target.name);

        setItemCategory({
            ...itemCategory,
            [e.target.name]: e.target.value
        });

        if (e.target.name === 'parentId') {
            // setGenuineParentId(e.target.value)
            var selectedIndex = e.nativeEvent.target.selectedIndex;
            const selectedCategoryName = e.nativeEvent.target[selectedIndex].text
            setSelectedRoute([...selectedRoute, { id: e.target.value, name: selectedCategoryName }])
        }
    }

    const _reset = () => {
        setSelectedRoute([]);
        setItemCategory({ ...itemCategory, parentId: '' })
        setCurrentCats([...[]]);

        getItemCatagories({ 'action': 'get', 'start': 0, 'end': 200, filter: 'parent' })
            .then(async res => {
                console.log(res);
                if (res.json.status) {
                    setCurrentCats(res.json.item_catagories)
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch(err => {
                console.log(err);
            })
    }

    const _submit = () => {
        //check if there are errors
        const { isValid, errors } = validateItemCategoryCreation(itemCategory);
        if (!isValid) {

            console.log(errors);
            setErrors({ ...errors })
            return;
        };

        setSubmitting(true);

        const reqestJson = {
            "action": 'add',
            "name": itemCategory.catName,
            "parent": selectedRoute.length > 0 ? selectedRoute[selectedRoute.length - 1].id : null
        }

        addItemCategory(reqestJson)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Item Category created successfully!', background: '#dffff0' })
                    try { props.history.push('/item-categories') } catch (err) {
                        const createdInstance = res.json.item_category[0]
                        props.setCreatedToSelected({
                            value: createdInstance.id,
                            label: createdInstance.name,
                        });
                        props.onHide();
                    }
                } else {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }

            }).catch((err) => {
                console.log(err)
                setSubmitting(false)
            });
    }
    const _cancel = () => {
        try { props.history.push('/item-categories') } catch (err) {
            props.onHide();
        }
    }


    console.log('to be sent is', itemCategory.catName, selectedRoute.length > 0 ? selectedRoute[selectedRoute.length - 1].id : null)

    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Create Item Category</h5>
                            <hr />
                            <Row>
                                <Col md={6}>

                                    <Form.Group  >
                                        <Form.Label>Category Name*</Form.Label>
                                        <Form.Control name="catName" value={itemCategory.catName} onChange={_change} type="text" placeholder="Category Name" />
                                        <ErrorBody>{errors.catName}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group >
                                        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                            <Form.Label>Parent Category</Form.Label>
                                            <div
                                                style={{ cursor: 'pointer' }}
                                                onClick={_reset}
                                            >
                                                <Badge style={{ padding: '3px 8px 5px' }} variant="primary">Reset</Badge>
                                            </div>
                                        </div>


                                        <Form.Control name="parentId" value={itemCategory.parentId} onChange={_change} as="select">

                                            <option value='' disabled >{parentLoading ? "Loading..." : "Select Parent Category"}</option>

                                            {currentCats.map((item, index) => {
                                                return <option key={index} value={item.id}>{item.name}</option>
                                            })}
                                        </Form.Control>

                                        <div style={{ margin: '4px 3px 4px 3px', color: '#42768a' }}>
                                            {selectedRoute.map((el, index) => {

                                                return (
                                                    <div key={index} style={{ display: 'inline' }} >
                                                        <span
                                                            key={index}
                                                        >
                                                            {el.name}
                                                        </span>
                                                        {
                                                            !(index === selectedRoute.length - 1) ?
                                                                (< i className="feather icon-chevron-right text-c-white f-15 " />) :
                                                                null
                                                        }
                                                    </div>
                                                );
                                            })}
                                        </div>

                                        <ErrorBody>{errors.parent}</ErrorBody>
                                    </Form.Group>

                                </Col>
                                <Col md={2}></Col>


                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={_cancel}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}
                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux >
    );
}

export default Create;
